export default {
  tableKey: 'name',
  classes: (item, index) => {
    return { 'table-success': item.current === item.limit }
  },
  schema: {
    index: {
      title: '#',
      classes: [],
      display: (data, index, item) => {
        return index + 1
      }
    },
    name: {
      title: '社團名稱',
      classes: []
    },
    limit: {
      title: '員額上限',
      classes: [],
      display: (data, index, item) => {
        return `${item.current}/${item.limit}`
      }
    },
    selection: {
      title: '入社人員',
      classes: ['text-start']
    }
  }
}
