<template>
  <div class="row">
    <div class="col">
      <div class="text-start">
        <span>☞</span>
        <span class="t-title link-primary" @click="clickTitle">
          {{ `${title} ${description}` }}
        </span>
        <span
          v-show="displayTable"
          class="t-title link-success"
          @click="exportCsv"
        >
          匯出
        </span>
      </div>
      <div class="table-responsive vertical-scroll">
        <table class="table">
          <thead v-show="displayTable">
            <tr>
              <th
                v-for="key in Object.keys(tableLayout.schema)"
                :key="key"
                :class="tableLayout.schema[key].classes"
              >
                {{ tableLayout.schema[key].title }}
              </th>
            </tr>
          </thead>
          <tbody v-show="displayTable">
            <tr
              v-for="(item, index) in tableItems"
              :key="item[tableLayout.tableKey]"
              :class="tableLayout.classes(item, index)"
            >
              <td
                v-for="key in Object.keys(tableLayout.schema)"
                :key="key"
                :class="tableLayout.schema[key].classes"
              >
                {{
                  tableLayout.schema[key].display
                    ? tableLayout.schema[key].display(item[key], index, item)
                    : item[key]
                }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TableBlock',
  props: {
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      default: ''
    },
    tableItems: {
      type: Array,
      required: true
    },
    tableLayout: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      displayTable: true
    }
  },
  methods: {
    clickTitle() {
      this.displayTable = !this.displayTable
    },
    exportCsv() {
      const csvContent = [
        ...[
          Object.keys(this.tableLayout.schema).map((key) => {
            return this.tableLayout.schema[key].title
          })
        ],
        ...this.tableItems.map((item, index) => {
          return Object.keys(this.tableLayout.schema).map((key) => {
            return this.tableLayout.schema[key].display
              ? this.tableLayout.schema[key].display(item[key], index, item)
              : item[key]
          })
        })
      ]
        .map((row) => row.join(','))
        .join('\r\n')

      const link = document.createElement('a')
      document.body.appendChild(link)
      link.href = URL.createObjectURL(
        new Blob([csvContent], {
          type: 'application/octet-stream'
        })
      )
      link.download = `${this.title}_${new Date().getTime()}.csv`
      link.click()
      link.parentNode.removeChild(link)
    }
  }
}
</script>

<style scoped>
.t-title {
  cursor: pointer;
}

.table {
  white-space: nowrap;
}
.vertical-scroll {
  max-height: 300px;
  overflow-y: auto;
  overflow-x: auto;
  margin-bottom: 40px;
}
</style>
