<template>
  <div id="app">
    <div class="container">
      <div class="row">
        <div class="col">
          <h4>竹光國中選社系統</h4>
        </div>
      </div>
      <hr />
      <div class="row">
        <div class="col">
          <div
            class="alert alert-success alert-dismissible fade show"
            role="alert"
            v-if="message"
          >
            {{ message }}
            <button
              type="button"
              class="btn-close"
              @click="() => (message = '')"
            ></button>
          </div>
        </div>
      </div>
      <div class="row" v-if="!selectFinish">
        <div class="col">
          <div class="mb-3 text-start">
            <label for="formFile" class="form-label">請匯入社團資料</label>
            <input class="form-control" type="file" @change="importClubCsv" />
          </div>
        </div>
      </div>
      <div class="row" v-if="!selectFinish">
        <div class="col">
          <div class="mb-3 text-start">
            <label for="formFile" class="form-label">請匯入學生資料</label>
            <input
              class="form-control"
              type="file"
              @change="importStudentCsv"
            />
          </div>
        </div>
      </div>
      <div
        class="row"
        v-if="isReadyToSelect && !selectFinish"
        style="margin-bottom: 16px"
      >
        <div class="col">
          <button
            type="button"
            class="btn btn-primary"
            @click="doSelectionProcess"
          >
            開始選社
          </button>
        </div>
      </div>
      <div class="row" v-if="selectFinish" style="margin-bottom: 16px">
        <div class="col">
          <!-- <button
            type="button"
            class="btn btn-primary"
            @click="doSelectionProcess"
          >
            重新選社
          </button> -->
          <button
            type="button"
            class="btn btn-primary"
            @click="resetData"
            style="margin-left: 16px"
          >
            重新匯入
          </button>
        </div>
      </div>
      <table-block
        v-if="selectFinish"
        :title="`社團抽籤結果`"
        :description="`${resultClubs?.reduce((r, c) => {
          return (r += c.current)
        }, 0)}/${resultClubs?.reduce((r, c) => {
          return (r += parseInt(c.limit, 10))
        }, 0)}`"
        :tableItems="resultClubs"
        :tableLayout="resultClubsLayout"
      />
      <table-block
        v-if="selectFinish"
        :title="`學生選社結果`"
        :description="`未中籤人數：${
          resultStudents.filter((e) => !e.club).length
        }人`"
        :tableItems="resultStudents"
        :tableLayout="resultStudentsLayout"
      />
    </div>
  </div>
</template>

<script>
import clubsLayout from '@/service/clubsLayout.js'
import studentsLayout from '@/service/studentsLayout.js'

import TableBlock from '@/components/TableBlock.vue'

export default {
  name: 'App',
  components: { TableBlock },
  data() {
    return {
      message: '',
      selectFinish: false,
      testClubs: [],
      testStudents: [],
      resultClubsLayout: clubsLayout,
      resultStudentsLayout: studentsLayout
    }
  },
  methods: {
    importCsv($event, onLoadFunc) {
      const file = $event.target.files[0]
      if (
        !file ||
        file.name.match(/\.[0-9a-z]+$/i)[0].toLocaleLowerCase() !== '.csv'
      ) {
        this.testClubs = []
        this.message = '請匯入csv檔'
      }
      const reader = new FileReader()
      reader.onload = () => {
        onLoadFunc(reader, $event.target)
      }
      reader.readAsText(file)
    },
    importClubCsv($event) {
      this.importCsv($event, (reader, target) => {
        let result = reader.result.split(/\r\n|\n/).reduce((r, c, i) => {
          // note. 若檔案要包含標頭就移除等號
          if (i >= 0 && c) {
            const dataArr = c.split(',').map((e) => e.trim())
            r.push({
              key: dataArr[1],
              name: dataArr[0],
              limit: dataArr[2],
              selection: []
            })
          }
          return r
        }, [])
        if (this.isDuplicate(result.map((e) => e.key))) {
          result = []
          target.value = ''
          this.message = '匯入資料包含重複的key值，請檢查後重新匯入'
        }
        this.testClubs = result
      })
    },
    importStudentCsv($event) {
      this.importCsv($event, (reader, target) => {
        let result = reader.result.split(/\r\n|\n/).reduce((r, c, i) => {
          // note. 若檔案要包含標頭就移除等號
          if (i >= 0 && c) {
            const dataArr = c.split(',').map((e) => e.trim())
            r.push({
              key: dataArr[1],
              name: dataArr[0],
              application: dataArr.slice(2),
              isDone: false
            })
          }
          return r
        }, [])
        if (this.isDuplicate(result.map((e) => e.key))) {
          result = []
          target.value = ''
          this.message = '匯入資料包含重複的key值，請檢查後重新匯入'
        }
        this.testStudents = result
      })
    },
    /**
     * 執行選課流程
     */
    doSelectionProcess() {
      const maxRound = this.testStudents.reduce((r, c) => {
        const len = c.application.length
        return r >= len ? r : len
      }, 0)
      let round = 0
      while (round <= maxRound) {
        this.selectionProcess(++round)
      }
      this.message = '選課完成'
      this.selectFinish = true
    },
    /**
     * 重設資料
     */
    resetData() {
      this.selectFinish = false
      this.testClubs = []
      this.testStudents = []
      document.querySelectorAll('input[type="file"]').forEach((e) => {
        e.value = ''
      })
    },
    /**
     * 選課流程
     * @param {number} round
     */
    selectionProcess(round) {
      // 取出當前輪次未選中志願的學生
      const candidateStudents = this.testStudents
        .filter((e) => !e.isDone && e.application[round - 1])
        .map((e) => {
          return {
            key: e.key,
            course: e.application[round - 1]
          }
        })

      // 將學生選入課程中
      this.testClubs = this.testClubs.map((course) => {
        if (course.limit > course.selection.length) {
          // 選出本課程候選學生
          const students = candidateStudents
            .filter((student) => student.course === course.key)
            .map((student) => student.key)

          // 計算差額
          const remaining = course.limit - course.selection.length

          // 選出中選學生
          const test = this.getMultipleRandom(students, remaining)
          course.selection = course.selection.concat(test).sort()
        }
        return { ...course }
      })

      // 將中選學生標注已選中志願
      this.testStudents = this.testStudents.map((student) => {
        if (!student.isDone) {
          const roundCourse = student.application[round - 1] || ''
          student.isDone =
            roundCourse &&
            this.testClubs
              .find((e) => e.key === roundCourse)
              ?.selection.some((e) => e === student.key)
        }
        return { ...student }
      })
    },
    getMultipleRandom(arr, num) {
      return [...arr].sort(() => 0.5 - Math.random()).slice(0, num)
    },
    isDuplicate(arr) {
      return arr.some((item, index) => arr.indexOf(item) != index)
    }
  },
  computed: {
    /** 是否準備好選社 */
    isReadyToSelect() {
      return this.testClubs.length && this.testStudents.length
    },
    resultClubs() {
      return this.testClubs
        .map((e) => {
          const selection = e.selection.map((s) => {
            return this.testStudents.find((t) => t.key === s)?.name || s
          })
          return {
            name: e.name,
            limit: e.limit,
            current: selection.length,
            selection: selection.join('、')
          }
        })
        .sort((a, b) => a.current - b.current)
    },
    resultStudents() {
      return this.testStudents.map((student) => {
        const club = this.testClubs.find((c) =>
          c.selection.some((s) => s === student.key)
        )?.key
        return {
          name: student.name,
          email: student.key,
          club: club,
          order: student.application.indexOf(club) + 1
        }
      })
    }
  },
  watch: {
    message(val) {
      if (val) {
        setTimeout(() => {
          this.message = ''
        }, 3000)
      }
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
